.bookmark {
  border: 0;
  outline: 0;
  width: fit-content;
  align-self: flex-end;
  background-color: #f5f5f5;
  height: 35px;
  width: 35px;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: 30px;
    height: 30px;
  }
}

